/* Global box-sizing */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Full screen styles */
html, body, #root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden; /* Prevents scrolling */
  font-family: Arial, sans-serif;
}



/* Keyframes for spinner rotation */
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Control keys styling */
.controlKeys {
  position: absolute;
  width: 11rem;
  left: 5%;
  bottom: 5%;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  font-size: 1rem;
  color: #fff;
}

/* Title text at the bottom right */
.title {
  right: 5%;
  font-size: 0.8rem;
  color: darkgray;
  position: absolute;
  width: 40%;
  bottom: 2%;
  text-align: center;
  font-weight: 600;
}

/* Title links */
.title > a {
  color: rgb(79, 189, 249);
  text-decoration: none;
  font-weight: bold;
}

.title > a:hover {
  color: rgb(0, 160, 255);
}
